/* 장터 글 목록페이지 CSS 파일*/

/*텍스트 UI 4개*/
.product_img_div{
    text-align: center;
}

.product_content{
    color: #000000;
    font-size : 0.9rem;
    /*text-align: center;*/
    margin : 4%;
}

.product_title2{
    color: #854141;
    font-size : 1.1rem;
    font-weight: 700;
    /*text-align: center;*/
    margin : 4%;
}

.product_des{
    font-size : 0.8rem;
    color : #311e1e;
    /*margin-right: 10px;*/
    /*padding-left: 10px;*/
    /*padding-right: 10px;*/
}
.product_mon{
    margin : 2%;
    font-size : 1.3rem;
    /*margin : 2% 3% 1% 4%;*/
    /*text-align: center;*/
    /*table-border-color-dark: #2c0b0e;*/
}

/*이미지 UI(크기)*/
.product_img{
    padding: 5px;
    width:80%;
    height:65%;
}

.product_text{
    margin : 4%;
    font-size : 0.7rem;
    /*margin : 2% 3% 1% 4%;*/
    /*text-align: center;*/
    /*table-border-color-dark: #2c0b0e;*/
}

.product_link_div{
    /*text-align: center;*/
}

.product_link{
    position:inherit;
    font-size : 1.2rem;
    font-weight: 500;
    text-decoration: none;
    background : #414648;
    color : white;
    border-radius : 10px;
    padding :5px;
}

/*검색창 UI*/
.search {
    width: 100%;
    height: 65%;
    margin-left: 2px;
    border-radius: 15px;
    border: 1px solid #bbb;
    padding: 10px 10px;
}

/*리스트 박스 UI*/
.box1 {
    color: #334c8d;
    box-shadow: 3px 7px 12px 2px #cdd2f3;
}

.box2 {
    color: #964a4a;
    box-shadow: 3px 7px 12px 2px #e8cfcd;
    /*box-shadow: 0px 0px 5px 0px #7e7e7e;*/
}

.box3 {
    background-color: #ffffff;
    opacity: 0.5;
}

.box4 {
    width: 100%;
    height: 100%;
    margin-left: 2px;
    border-radius: 15px;
    border: 1px solid #a2a29d;
    padding: 10px 10px;
}

.bi-cart4 {
    font-size: 35px;
    line-height: 22px;
}